<template>
    <div>
        <main>
        <div class="title">
            <h1>Tableau de bord</h1>
        </div>
        <div class="card content users">
            <h2>Connecté en tant que {{currentUser.name}}</h2>
            <p>
                <small>Compte créé le {{getDate}}</small><br>
            </p>
            <button @click="$router.push('/dashboard/trash')">Accéder à la poubelle</button>
            <button @click="$router.push('/dashboard/users')">Gestion des utilisateurs</button>
            <button @click="$router.push('/dashboard/logs')">Voir les logs</button>
        </div>
        </main>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    computed: {
        ...mapGetters('users',[
            'currentUser'
        ]),
        getDate: function() {
            var d = new Date(Date.parse(this.currentUser.created_at));
                return d.toLocaleDateString('fr-FR', { weekday:'long', year:'numeric', month:'long', day:'numeric', hour: 'numeric', minute:'numeric' });
        }
    }
}
</script>
<style scoped>
button {
    outline: none;
    transition: all .3s;
    user-select: none;
    cursor: pointer;
    display: inline-block;
    border-radius: 7px;
    border: none;
    font-size: 15px;
    background: var(--b3);
    border: 1px solid var(--b3);
    color: var(--b8);
    margin: 10px 15px 0 0;
    padding: 10px 16px;
}

button.outline {
    background: none;
    color: var(--b3);
}
button:hover { 
    border: 1px solid var(--b2);
    background: var(--b2);
}
button.outline:hover { 
    border: 1px solid var(--b3);
    background: var(--b3);
    color: var(--b8);
}
  
</style>